<template>
  <v-container fluid fill-height style="padding:0;" id="webSecurity" grid-list-md>
    <Blog :items="items" :time="time" :title="title">
      <div class="blogcontent">
        <v-layout row wrap>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="security">常见安全问题</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <ul>
                        <li>
                            <p class="subtitle-1"><span class="font-weight-bold">注入式攻击</span></p>
                            <p class="body-2">概念：将用户输入注入到后端数据库的sql语句、shell命令或者操作系统调用，就会出现注入式攻击</p>
                            <p class="body-2">预防：需要对用户输入做无害化处理，如特殊字符转义</p>
                        </li>
                        <li>
                            <p class="subtitle-1"><span class="font-weight-bold">Cross-Site Scripting (XSS) 跨站脚本攻击</span></p>
                            <p class="body-2">概念：跨站点脚本攻击(XSS)是一种客户端代码注入攻击。
                                        攻击者的目的是通过在合法的web程序中包含恶意代码，在受害者的web浏览器中执行恶意脚本。
                                        恶意脚本可以获取cookies，伪装成用户从而发动CSRF攻击。
                                        易受XSS攻击的包括论坛、留言板和允许评论的web页面。
                            </p>
                            <p class="body-2">种类：
                                <ol>
                                    <li>持久型XSS(持久型)：将恶意代码存储到服务器中，如黑客可以通过请求传参的方式将恶意代码上传到网站</li>
                                    <li>反射型XSS(非持久型)：通过社会工程等手段诱使用户点击带有恶意代码的链接，这种方法需要用户直接参与</li>
                                    <li>基于DOM的XSS：将从服务器中获取的恶意代码直接写入到dom元素中，或者直接使用url参数</li>
                                </ol>
                            </p>
                            <p class="body-2">预防：对页面上的用户输入和输出以及服务器传递的数据做无害化处理。设置httponly从而禁止通过js获取cookie</p>
                        </li>
                        <li>
                            <p class="subtitle-1"><span class="font-weight-bold">Cross Site Request Forgery (CSRF) 跨站请求伪造</span></p>
                            <p class="body-2">概念：用户通过浏览器登陆了正常的网站但是没有注销，然后又浏览了黑客的网站，黑客网站发出请求到用户浏览的正常网站从而允许攻击者伪装用户发起请求</p>
                            <p class="body-2">预防：
                                <ol>
                                    <li>在敏感数据请求需要做重新认证的操作</li>
                                    <li>如果你不想重新认证可以在请求中包含随机的token，这个token不能使用cookie存储，可以将其保存在隐藏的html标签上</li>
                                </ol>
                            </p>
                        </li>
                        <li>
                            <p class="subtitle-1"><span class="font-weight-bold">文件上传漏洞</span></p>
                            <p class="body-2">概念：攻击者上传了一个可执行的文件到服务器并执行，如php，jsp，exe等文件</p>
                            <p class="body-2">预防：
                                <ol>
                                    <li>客户端检测：使用js对上传文件检测（文件大小、文件扩展名、文件类型等）</li>
                                    <li>服务端检测：
                                        <ul>
                                            <li>对文件大小、文件路径、文件扩展名、文件类型检测</li>
                                            <li>对文件内容检测</li>
                                            <li>对文件重命名</li>
                                        </ul>
                                    </li>
                                    <li>服务器端上传目录设置不可执行权限</li>
                                    <li>检查网站的文件解析漏洞（扩展名和文件名的解析异常）和文件包含漏洞（后端语言导入并执行恶意文件）</li>
                                </ol>
                            </p>
                        </li>
                    </ul>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </div>
    </Blog>
  </v-container>
</template>

